.header {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 20;
    font-family: 'Inter', sans-serif;
    background-color: white;
    box-shadow: 1px 0 1px #7E74F1;

    &__link {
        font-size: 16px;
        line-height: normal;
        color: #232E35;
        padding: 8px 20px;
        border: 1px solid #F1F1F1;
        background-color: white;
        border-radius: 8px;
        font-weight: 500;
        transition: background-color 0.35s;

        &:hover {
            @media (min-width: 1229px) {
                background-color: #f4f4f5;
            }
        }

        &:active {
            background-color: #f4f4f5;
        }

        svg {
            display: none;
        }
    }
}

@media (max-width: 798px) {
    .header {

        &__link {
            display: flex;
            align-items: center;
            justify-content: center;
            width: 40px;
            height: 40px;
            padding: 0;
            font-size: 20px;
            border-color: #D9D9D9;


            span {
                display: none;
            }

            svg {
                display: unset;
            }
        }
    }
}