.career {
    background-color: #FBFBFB;

    &__inner {
        display: flex;
        justify-content: space-between;
    }

    &__left {
        display: flex;
        flex-direction: column;
        max-width: 360px;
    }

    &__right {
        width: 100%;
        max-width: 700px;
        position: relative;
    }

    &__header {
        display: flex;
        flex-direction: column;
    }
}

@media (max-width: 1230px) {

    .career {

        &__right {
            max-width: 560px;
        }
    }
}

@media (max-width: 1022px) {
    .career {

        &__inner {
            flex-direction: column;
        }

        &__left {
            display: flex;
            flex-direction: row;
            max-width: unset;
            // align-items: center;
            justify-content: space-between;
            margin-bottom: 72px;
        }

        &__right {
            max-width: unset;
        }

        &__header {
            max-width: 350px;
            width: 100%;
        }
    }
}

@media (max-width: 606px) {
    .career {

        &__left {
            flex-direction: column;
            margin-bottom: 42px;
        }

        &__header {
            margin-bottom: 32px;
        }
    }
}