.portfolio {
    background-color: white;

    &__inner {
        display: flex;
        flex-direction: column;
    }

    &__header {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 120px;
    }

    &__left {
        display: flex;
        flex-direction: column;
    }

    &__btns {
        display: flex;
    }

    &__slider {
        width: 100%;
    }
}

@media (max-width: 1022px) {
    .portfolio {

        &__header {
            margin-bottom: 64px;
        }
    }
}

@media (max-width: 798px) {
    .portfolio {

        &__header {
            align-items: flex-end;
        }
    }
}

@media (max-width: 606px) {
    .portfolio {
        padding: 42px 0;

        &__header {
            flex-direction: column;
            align-items: unset;
            margin-bottom: 42px;
        }

        &__btns {
            margin-top: 18px;
        }
    }
}