.greeting {
    padding: 0;
    display: flex;
    height: 100vh;
    align-items: center;
    font-family: 'Inter', sans-serif;

    &__inner {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    &__left {
        display: flex;
        flex-direction: column;
        max-width: 540px;
    }

    &__description {
        font-size: 16px;
        font-style: normal;
        font-weight: 400;
        line-height: 200%;
        color: #232e35;
        margin-bottom: 42px;
    }

    &__right {
    }

    &__photo {
        max-width: 340px;
        height: 400px;
        border-radius: 16px;
        width: 100%;
        background-color: #eae6fe;
        position: relative;

        &::after {
            content: "";
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background-color: #7e74f1;
            border-radius: 16px;
            transform: rotate(-9deg);
            z-index: -1;
            transition: transform 0.45s;
        }

        &:hover {
            @media (min-width: 1229px) {
                &::after {
                    transform: rotate(-15deg);
                }
            }
        }
    }

    &__img {
        position: absolute;
        right: 0;
        bottom: 0;
        overflow: hidden;
        border-radius: 16px;
    }
}

@media (max-width: 1022px) {
    .greeting {
        // height: unset;
        // align-items: unset;
        padding-top: 150px;

        &__inner {
            flex-direction: column-reverse;
        }

        &__photo {
            margin-bottom: 72px;
            max-width: 260px;
            height: 320px;
        }
    }
}

@media (max-width: 606px) {
    .greeting {
        // padding-bottom: 42px;

        &__description {
            font-size: 14px;
            line-height: 170%;
        }
    }
}